    .seeYouSoonFrame {
        position: absolute;
        bottom: 3vh;
        width: 80%;
        padding-top: 2vh;
        padding-bottom: 1vh;
        border-radius: 2vh;
        border: 0.3vh solid #caf5ff;;
        box-shadow:
            0 0 1vh var(--aqua),
            0 0 2vh var(--aqua),
            0 0 3vh var(--aqua),
            inset 0 0 1vh var(--aqua),
            inset 0 0 2vh var(--aqua);
    }

    .seeYouSoon {
        font-family: titleone;
        text-align: center;
        font-size: 3.5vh;
        color: #ffabab;
        text-shadow:
            0 0 0.5vh var(--red),
            0 0 1vh var(--red),
            0 0 1.5vh var(--red),
            0 0 2vh var(--red),
            0 0 2.5vh var(--red),
            0 0 3vh var(--red),
            0 0 4vh var(--red);
        opacity: 100%;
        animation: seeYouSoonLight 2s infinite;
    }
    
    @keyframes seeYouSoonLight {
        0% {opacity: 100%;}
        50% {opacity: 100%;}
        52% {opacity: 0%;}
        54% {opacity: 100%;}
        56% {opacity: 0%;}
        58% {opacity: 100%;}
        100% {opacity: 100%;}
    }

